@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "ChaoYanZeTeMaoKai";
  src: local("ChaoYanZeTeMaoKai"),
    url(/mathquestionsite/static/media/ChaoYanZeTeMaoKai.bdb9a35f.woff) format("woff");
}

@font-face {
  font-family: "ArphicHeitiBold";
  src: local("ArphicHeitiBold"),
    url(/mathquestionsite/static/media/ArphicHeitiBold.947ae141.woff) format("woff");
}

@font-face {
  font-family: "ArphicHeitiLight";
  src: local("ArphicHeitiLight"),
    url(/mathquestionsite/static/media/ArphicHeitiLight.e88ffddb.woff) format("woff");
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.ql-editor{
  min-height: 100px !important;
  /* max-height: 300px; */
 
}
